.main-menu {
  justify-content: space-evenly;
  align-items: center;
  gap: 0 15px;
  margin: 0;
  padding: 0;
  display: flex;
}

.main-menu a {
  text-align: left;
  color: #fff;
  text-transform: capitalize;
  font-size: 16px;
  font-weight: 500;
}

.main-menu a:hover {
  text-decoration: underline;
}

.main-menu li {
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.main-menu li.menu-item-has-children {
  position: relative;
}

.main-menu li.menu-item-has-children:after {
  content: url("../dropdown-down.8bd34bfe.svg");
  position: relative;
  top: -1px;
  left: 5px;
}

.main-menu li.menu-item-has-children:hover .sub-menu.lvl-1 {
  display: block;
}

.current-menu-item > a {
  text-decoration: underline;
}

@media (max-width: 1024px) {
  .menu-main-menu-container {
    width: 66.6667%;
  }
}

.sub-menu.lvl-1 {
  --tw-bg-opacity: 1;
  background-color: rgb(4 30 66 / var(--tw-bg-opacity));
  padding: 0;
  position: absolute;
}

@media (max-width: 1024px) {
  .sub-menu.lvl-1 {
    --tw-bg-opacity: 1;
    background-color: rgb(38 76 109 / var(--tw-bg-opacity));
  }
}

.sub-menu.lvl-1 {
  min-width: 300px;
  display: none;
  top: 100%;
  left: -5px;
}

.sub-menu.lvl-1 li {
  justify-content: flex-start;
  align-items: flex-start;
  padding: 12px;
}

.sub-menu.lvl-1 li.menu-item-has-children:after {
  left: 0;
  transform: rotate(-90deg);
}

.sub-menu.lvl-1 li.menu-item-has-children:hover .sub-menu.lvl-2 {
  display: block;
}

@media (max-width: 1024px) {
  .sub-menu.lvl-1 li.menu-item-has-children:hover .sub-menu.lvl-2 {
    display: none;
  }
}

.sub-menu.lvl-1 a {
  width: 100%;
  font-size: 17px;
  display: block;
}

.sub-menu.lvl-2 {
  --tw-bg-opacity: 1;
  background-color: rgb(38 76 109 / var(--tw-bg-opacity));
  min-width: 300px;
  padding: 0;
  display: none;
  position: absolute;
  top: -11px;
  left: 100%;
}

@media (max-width: 1024px) {
  .sub-menu.lvl-2 {
    top: 0;
    left: 0;
  }
}

.sub-menu.lvl-2 li {
  justify-content: flex-start;
  align-items: flex-start;
}

.sub-menu.lvl-2 a {
  font-size: 17px;
}

:root {
  --bg-blue: #000261;
  --bg-darkblue: #041e42;
  --bg-semiblue: #264c6d;
}

#main-menu-button {
  cursor: pointer;
  vertical-align: middle;
  width: 66px;
  height: 64px;
  text-align: center;
  text-transform: uppercase;
  -webkit-user-select: none;
  user-select: none;
  background-color: var(--bg-darkblue);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 5px 2px;
  font-size: 30px;
  text-decoration: none;
  transition: all .2s ease-in-out;
  display: flex;
  position: relative;
}

#main-menu-button span[class^="ico-"] {
  transform-origin: center;
  color: #fff;
  will-change: opacity, transform;
  margin-right: 10px;
  transition: all .2s;
  position: absolute;
  top: 50%;
  left: 50%;
}

#main-menu-button span {
  margin: 0;
}

#main-menu-button .open-menu {
  transform: translate(-50%, -50%);
}

#main-menu-button .close-menu {
  opacity: 0;
  transform: translate(-50%, -50%)rotate(-45deg);
}

#main-menu-button.open .close-menu {
  opacity: 1;
  transform: translate(-50%, -50%);
}

#main-menu-button.open .open-menu {
  opacity: 0;
  transform: translate(-50%, -50%)rotate(45deg);
}

nav.main-menu {
  z-index: 309;
  height: calc(100vh - 64px);
  pointer-events: none;
  scroll-behavior: smooth;
  width: 100%;
  position: fixed;
  inset: 64px -15px 0 0;
  overflow: hidden;
}

nav.main-menu button.link:hover {
  color: #003c3f;
}

nav.main-menu .wrapper {
  height: calc(100vh - 64px);
  min-height: calc(100vh - 64px);
  width: 100%;
  flex-flow: row;
  display: flex;
  position: fixed;
  top: 64px;
  overflow: hidden;
}

@media (max-width: 1024px) {
  nav.main-menu .wrapper {
    overflow-y: scroll;
  }
}

nav.main-menu ul {
  height: auto;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}

nav.main-menu ul.visible {
  display: block;
}

nav.main-menu a:not(.btn) {
  color: #fff;
  border-left: 2px solid #0000;
  padding: 2px 15px;
  font-size: 20px;
  text-decoration: none;
}

nav.main-menu a.active {
  color: var(--bg-darkblue);
  border-color: var(--bg-darkblue);
  font-weight: 700;
}

nav.main-menu li {
  height: auto;
  border-bottom: 1px solid #fff;
  grid-template-columns: 1fr auto;
  margin-bottom: 0;
  padding: 9px 0;
  display: grid;
}

nav.main-menu [class*="level-"] {
  z-index: 5;
  opacity: 0;
  will-change: transform, opacity;
  pointer-events: none;
  min-height: 100%;
  border-right: 1px solid #fff;
  flex: 0 0 300px;
  transition: all .2s ease-in-out;
  display: block;
  transform: translateX(-100%);
  height: -moz-fit-content !important;
  height: fit-content !important;
}

nav.main-menu button.open-submenu {
  color: #fff;
  width: 45px;
  background: none;
  border: none;
  outline: none;
  font-size: 1.35rem;
  position: relative;
}

nav.main-menu button.open-submenu [class*="ico-"] {
  transform-origin: center;
  color: #fff;
  will-change: opacity, transform;
  font-size: 1.5rem;
  transition: all .2s;
  position: absolute;
  top: 50%;
  left: 50%;
}

nav.main-menu button.open-submenu .open-menu {
  transform: translate(-50%, -50%);
}

nav.main-menu button.open-submenu .close-menu {
  opacity: 0;
  transform: translate(-50%, -50%)rotate(-45deg);
}

nav.main-menu button.open-submenu.open .close-menu {
  opacity: 1;
  transform: translate(-50%, -50%);
}

nav.main-menu button.open-submenu.open .open-menu {
  opacity: 0;
  transform: translate(-50%, -50%)rotate(45deg);
}

nav.main-menu button.open-submenu.open span {
  color: #fff;
}

nav.main-menu .level-1 {
  background: var(--bg-darkblue);
  z-index: 7;
  padding: 0;
  transition: all .2s ease-in-out;
  transform: translateX(-100%);
}

nav.main-menu .level-1 ul {
  display: block;
}

nav.main-menu .level-1 li {
  border-color: #264c6d;
}

nav.main-menu .level-2 {
  background: var(--bg-blue);
  z-index: 6;
}

nav.main-menu .level-2 li {
  border-color: #264c6d;
  padding-left: 15px;
}

nav.main-menu .level-2 button.open-submenu {
  border-color: #264c6d;
}

nav.main-menu .level-3 {
  background: var(--bg-semiblue);
  z-index: 5;
}

nav.main-menu .level-3 li {
  border-color: #264c6d;
  padding-left: 15px;
}

nav.main-menu .level-3 button.open-submenu {
  border-color: var(--bg-darkblue);
}

nav.main-menu li.mobile-close-level {
  display: none;
}

nav.main-menu [data-level="2"] button.close-level, nav.main-menu [data-level="3"] button.close-level {
  background: var(--bg-darkblue);
}

nav.main-menu .lang-switcher {
  width: 100%;
  justify-content: center;
  display: flex;
}

nav.main-menu .lang-switcher a {
  border-left: 0;
}

nav.main-menu.open {
  pointer-events: all;
  overflow-x: hidden;
  overflow-y: scroll;
}

nav.main-menu.open .level-1, nav.main-menu.active-level-2 [data-level="2"], nav.main-menu.active-level-3 [data-level] {
  opacity: 1;
  pointer-events: all;
  transform: none;
}

.level-3.open {
  background: var(--bg-semiblue);
}

@media (max-width: 1200px) {
  header.main .wrapper {
    margin: 0;
  }
}

@media (max-width: 1100px) {
  header.main {
    height: 60px;
  }
}

@media (min-width: 1026px) {
  nav.main-menu a:hover {
    border-left: 2px solid #fff;
    text-decoration: none;
  }
}

@media (max-width: 1025px) {
  nav.main-menu ul {
    overflow: hidden;
  }

  nav.main-menu li.mobile-close-level {
    display: grid;
  }

  nav.main-menu button.close-level {
    text-align: left;
    color: #fff;
    text-transform: uppercase;
    border: none;
    align-items: center;
    padding: 10px;
    font-size: 1.2rem;
    display: flex;
  }

  nav.main-menu button.close-level [class*="ico-"] {
    margin-right: 10px;
    font-size: 1.4rem;
  }

  nav.main-menu [class*="level-"] {
    height: auto;
    flex: 1 0 300px;
    padding-bottom: 25px;
    left: 0;
  }

  nav.main-menu .level-1 {
    background: var(--bg-darkblue);
    flex: 1 0 345px;
  }

  nav.main-menu .level-2 {
    background: var(--bg-darkblue);
  }

  nav.main-menu .level-3 {
    background: var(--bg-darkblue);
    transform: translateX(-200%);
  }

  nav.main-menu.active-level-2 [data-level="1"] {
    transform: translateX(-100%);
  }

  nav.main-menu.active-level-2 [data-level="2"] {
    transform: translateX(calc(-100% - 45px));
  }

  nav.main-menu.active-level-3 [data-level="1"] {
    transform: translateX(-100%);
  }

  nav.main-menu.active-level-3 [data-level="2"], nav.main-menu.active-level-3 [data-level="3"] {
    transform: translateX(-215%);
  }
}

@media (max-width: 450px) {
  nav.main-menu {
    right: 0;
  }

  nav.main-menu [class*="level-"] {
    flex: 1 0 calc(100% - 45px);
  }

  nav.main-menu [class*="level-1"] {
    flex: 1 0 100%;
  }
